.location-cell:hover {
  background: #F5F5F5;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}

.location-cell:hover .location-title {
  color: #151515;
}

.location-title {
  letter-spacing: 10px;
  padding-top: 120px;
  font-weight: 600;
  color: #F5F5F5;
}

.active {
  background: #F5F5F5;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  color: #151515;
}
.active .card-title {
  color: #151515;
  text-align: left;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;

}
.active > h3 {
  color: #151515
}

.card-deck {
  width: 100%;
}



